* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: geometricPrecision;
}

/* reset styles */
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100%;
}

/* default styles */
body {
  font-family: 'PT Root UI', sans-serif;
}

.view {
  padding: 20px;
  width: 1024px;
  margin: 0 auto;
}

.view:after {
  content: '';
  display: table;
  clear: both;
}

.view div {
  float: left;
  width: 50%;
  padding: 10px;
}
